<h2>属性</h2>
<h3><code v-pre>vc-geometry-ellipsoid</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>radii</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定椭球体在x、y、z方向上的半径。 <strong>结构: { x: number, y: number, z: number }</strong></td>
</tr>
<tr>
<td>innerRadii</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定椭球体在x、y、z方向上的内半径。</td>
</tr>
<tr>
<td>minimumClock</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定椭球体在xy平面内从x轴到y轴的最小角度。</td>
</tr>
<tr>
<td>maximumClock</td>
<td>Number</td>
<td><code v-pre>2*PI</code></td>
<td><code v-pre>optional</code> 指定椭球体在xy平面内从x轴到y轴的最大角度。</td>
</tr>
<tr>
<td>minimumCone</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定椭球体从z轴正半轴到z轴负半轴的最小角度。</td>
</tr>
<tr>
<td>maximumCone</td>
<td>Number</td>
<td><code v-pre>PI</code></td>
<td><code v-pre>optional</code> 指定椭球体从z轴正半轴到z轴负半轴的最大角度。</td>
</tr>
<tr>
<td>stackPartitions</td>
<td>Number</td>
<td><code v-pre>64</code></td>
<td><code v-pre>optional</code> 指定将椭球体横向划分为层的次数。</td>
</tr>
<tr>
<td>slicePartitions</td>
<td>Number</td>
<td><code v-pre>64</code></td>
<td><code v-pre>optional</code> 指定将椭球体纵向划分为片的次数。</td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定椭球体顶点属性渲染方式。</td>
</tr>
</tbody>
</table>
<hr>
<h3><code v-pre>vc-geometry-outline-ellipsoid</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>radii</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定椭球体在x、y、z方向上的半径。 <strong>结构: { x: number, y: number, z: number }</strong></td>
</tr>
<tr>
<td>innerRadii</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定椭球体在x、y、z方向上的内半径。</td>
</tr>
<tr>
<td>minimumClock</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定椭球体在xy平面内从x轴到y轴的最小角度。</td>
</tr>
<tr>
<td>maximumClock</td>
<td>Number</td>
<td><code v-pre>2*PI</code></td>
<td><code v-pre>optional</code> 指定椭球体在xy平面内从x轴到y轴的最大角度。</td>
</tr>
<tr>
<td>minimumCone</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定椭球体从z轴正半轴到z轴负半轴的最小角度。</td>
</tr>
<tr>
<td>maximumCone</td>
<td>Number</td>
<td><code v-pre>PI</code></td>
<td><code v-pre>optional</code> 指定椭球体从z轴正半轴到z轴负半轴的最大角度。</td>
</tr>
<tr>
<td>stackPartitions</td>
<td>Number</td>
<td><code v-pre>10</code></td>
<td><code v-pre>optional</code> 指定将椭球体横向划分为层的次数。</td>
</tr>
<tr>
<td>slicePartitions</td>
<td>Number</td>
<td><code v-pre>8</code></td>
<td><code v-pre>optional</code> 指定将椭球体纵向划分为片的次数。</td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定椭球体顶点属性渲染方式。</td>
</tr>
<tr>
<td>subdivisions</td>
<td>Number</td>
<td><code v-pre>128</code></td>
<td><code v-pre>optional</code> 指定椭球体轮廓线上的点数，确定弧线的光滑粒度。</td>
</tr>
</tbody>
</table>
<p>参考官方文档: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/EllipsoidGeometry.html">EllipsoidGeometry</a></strong>、 <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/EllipsoidOutlineGeometry.html">EllipsoidOutlineGeometry</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          geometry: null,
          attributes: null,
          modelMatrix: null,
          vertexFormat: null,
          radii: { x: 200000.0, y: 200000.0, z: 300000.0 },
          modelMatrixOutline: null
        }
      },
      mounted() {
        Promise.all([this.$refs.ellipsoid.createPromise, this.$refs.ellipsoidOutline.createPromise]).then((instances) => {
          console.log('All geometries are loaded.')
          const boundingSphereUnion = instances.reduce((prev, cur) => {
            const geometry = cur.cesiumObject.constructor.createGeometry(cur.cesiumObject)
            const boundingSphere = cur.vm.$parent.modelMatrix
              ? Cesium.BoundingSphere.transform(geometry.boundingSphere, cur.vm.$parent.modelMatrix)
              : geometry.boundingSphere
            return prev === null ? boundingSphere : Cesium.BoundingSphere.union(prev, boundingSphere)
          }, null)
          instances[0].viewer.scene.camera.flyToBoundingSphere(boundingSphereUnion)
        })
      },
      methods: {
        ready({ Cesium, viewer }) {
          this.viewer = viewer
          const { Matrix4, PerInstanceColorAppearance, Transforms, Cartesian3, ColorGeometryInstanceAttribute } = Cesium
          this.appearance = new PerInstanceColorAppearance({
            flat : true
          })
          this.vertexFormat = PerInstanceColorAppearance.VERTEX_FORMAT
          this.attributes = {
            color: new ColorGeometryInstanceAttribute(Math.random(), Math.random(), Math.random(), 0.5)
          }
          this.modelMatrix = Matrix4.multiplyByTranslation(
            Transforms.eastNorthUpToFixedFrame(Cartesian3.fromDegrees(105.0, 35.0)),
            new Cartesian3(0, 0, 100000),
            new Matrix4()
          )
          this.modelMatrixOutline = Matrix4.multiplyByTranslation(
            Transforms.eastNorthUpToFixedFrame(Cartesian3.fromDegrees(110.0, 35.0)),
            new Cartesian3(0, 0, 200000),
            new Matrix4()
          )
        },
        LEFT_CLICK(movement) {
          const feature = this.viewer.scene.pick(movement.position)
          console.log(feature)
        }
      }
    }
  </script>